.tecnology{
        width: 80%;
        height: 85vh;
        float: right;
        position: relative;
        overflow: scroll;
}
.tecnology::-webkit-scrollbar {
    /*隐藏滚轮*/
    display: none;
    }
.tecnology-header{
    width: 70vw;
    display: flex;
    height: 2vh
}
.tecnology-line{
    width: 75vw;
    height: 0.5vh;
    background-color: #fff;
    margin-top: 1vw;
    margin-bottom: 3vw;
}
.tecnology-return{
    flex: 2;
    position: absolute;
    right: 2vw;
    /* 多用定位少用边距来挪位置 */
    top: 0.1vw;
}
.tecnology-return a{
    /* 字体颜色设置透明替代设置整个盒子透明 */
    color: rgb(194, 206, 218,0.6);
    font-weight: 300;
    font-size: 1.2vw;
    display: block;
}
.tecnology-share{
    flex: 8;
    font-size: 1.5vw;
    font-weight: 600;
    color: rgb(255, 255, 255);
}
.tecnology-content-img img{
    width: 15vw;
    height: 12vw;
}
.tecnology-content-img{
    display: inline-block;
}
.tecnology-content{
    width: 60vw;
    height: 12vw;
    margin-bottom: 10vh;
    position: relative;
    left: 5vw;
    border: 0.3vw solid rgb(56, 63, 78);
    background-color: rgb(138, 199, 220);
    box-sizing: content-box !important; 
}
.tecnology-content:hover{
    background-color: rgb(122, 224, 146);
}
.tecnology-content-text{
    color: darkslategray;
    position: absolute;
    left: 15vw;
    top: 4vw;
    font-size: 0.9vw;
    padding-left: 2vw;
    width: 40vw;
    /* 多行省略 */
    overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 3;
	-webkit-box-orient: vertical;
}
.tecnology-content-title{
    display: inline-block;
    color: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 0.5vw;
    font-weight: 600;
    font-size: 1.5vw;
    padding-left: 2vw;
}
.tecnology-content-detail{
    position: absolute;
    bottom: 0;
    right: 0;
    font-size: 1vw;
    color:rgb(201, 31, 136)
}